.contact{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-self: center;
    margin-top: 5%;
    text-align: center;
}
.contact__title,
.contact__text{
    display: flex;
    width: 100%;
    justify-content: center;
    color: black;
}

.contact__links{
    place-content: center;
    margin-top: 1%;
}
.contact__link{
    padding: 1%;
}

@media screen and (max-width: 767px) {
    .contact__links{
        place-content: center;
        margin-top: 2%;
        display: flex;
        flex-direction: initial;
        flex-wrap: wrap;
    }
}